$yellow: #ffc93e;
$grad: linear-gradient(268.41deg, $yellow 0%, #5833ee 100%);
$text_black: #444;
:root {
  --index: calc(1vw + 1vh);
}

// общие стили
body {
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: $text_black;
  font-style: normal;
  font-size: 18px;
  line-height: 24px;
  font-weight: 500;
  overflow-x: hidden;
  &.none_bg {
    &:after {
      content: none;
    }
  }
}

b, strong {
  font-weight: bold;
}

section, .mb-sect {
  margin-bottom: 170px;
}
.no-height {
  height: 0 !important;
}
.wrapper {
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding: 0 100px;
}

.wrapper_small {
  margin-left: auto;
  margin-right: auto;
  max-width: 1360px;
}

.btn {
  text-align: center;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border: none;
  color: #000;
  padding: 20px 40px;
  min-width: 280px;
  display: inline-block;
  background: $yellow;
  border-radius: 60px;
  transition: all .3s;
  font-size: calc(var(--index) * 1.1);
  font-weight: bold;

  &::after {
    top: -32px;
    content: '';
    display: block;
    -ms-transform: rotate(25deg);
    transform: rotate(25deg);
    position: absolute;
    width: 20px;
    height: 130px;
    z-index: 2;
    opacity: .55;
    background: #fff;
    -webkit-animation: 2.8s ease-out infinite btn-effect;
    animation: 2.8s ease-out infinite btn-effect;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 20px 70px rgba(231, 178, 42, 0.3);
  }
}

.-hide {
  display: none !important;
}

@keyframes btn-effect {
  0% {
    left: -20%
  }
  100%, 35% {
    left: 110%
  }
}

// POPUP
.fancybox__content {
  border-radius: 10px;
}

.popup {
  display: none;

  &_wrapper {
    max-width: 440px;
    width: 100%;
  }

  .form {
    flex-direction: column;
    align-items: center;

    div {
      max-width: 300px;
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
        margin-top: 10px;
      }

      input {
        max-width: 100%;
        width: 100%;
        display: block;
      }
    }

    &-wrap {
      padding: 0;
    }

    .btn {
      display: block;
      margin: 0 auto;
    }
  }
}

.flex {
  display: flex;
}

.flex-3 {
  display: flex;
  align-items: center;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.grid-4 {
  justify-content: center;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.bread {
  margin-left: calc(var(--index) * -4);
  margin-bottom: calc(var(--index) * 0.9);

  ol {
    display: flex;

    li {
      font-size: calc(var(--index) * 0.6);
      margin-right: 10px;

      &:not(:last-child):after {
        content: " – ";
        display: inline-block;
        margin-left: 5px;
      }

      a {
        color: $text_black;
      }
    }
  }
}

._sb {
  justify-content: space-between;
}

.yellow_line {
  position: relative;

  &:before {
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    bottom: 0;
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background: $yellow;
    border-radius: 4px;
  }

  &_title {
    position: relative;

    &:before {
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      bottom: 0;
      content: "";
      display: block;
      width: 100%;
      height: 8px;
      background: $yellow;
      border-radius: 4px;
    }
  }

  &_text {
    color: $text_black;
    overflow: hidden;
    position: relative;

    &:before {
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      content: "";
      display: block;
      background: $yellow;
      border-radius: 4px;
      bottom: -2px;
      height: 6px;
      width: 105%;
    }

    &::after {
      top: -32px;
      content: '';
      display: block;
      -ms-transform: rotate(25deg);
      transform: rotate(25deg);
      position: absolute;
      width: 10px;
      height: 130px;
      z-index: 2;
      opacity: .95;
      background: #fff;
      -webkit-animation: 2.5s ease-out infinite btn-effect;
      animation: 2.5s ease-out infinite btn-effect;
    }
  }
}

.-whats_app {
  position: relative;
  overflow: hidden;

  &::after {
    top: -32px;
    content: '';
    display: block;
    -ms-transform: rotate(25deg);
    transform: rotate(25deg);
    position: absolute;
    width: 10px;
    height: 130px;
    z-index: 2;
    opacity: .95;
    background: #fff;
    -webkit-animation: 2.5s ease-out infinite btn-effect;
    animation: 2.5s ease-out infinite btn-effect;
  }
}

.h2 {
  text-align: center;
  color: #000;
  font-size: calc(var(--index) * 1.9);
  line-height: 52px;
  font-weight: bold;
  span {
    color: $yellow;
  }
}

.h3 {
  text-align: center;
  color: #000;
  font-size: calc(var(--index) * 1.2);
  line-height: 47px;
  font-weight: bold;
}

.sub_title {
  font-size: 25px;
  margin-top: 10px;
  line-height: 38px;
  text-align: center;
  span {
    color: $yellow;
  }
}

.mb-80 {
  margin-bottom: 80px;
}

.inp_text {
  display: block;
  border: 1px solid #999;
  padding: 20px;
  border-radius: 40px;
  text-align: center;
}

.politica {
  display: block;
  line-height: 30px;
  text-align: center;
  letter-spacing: .05em;
  color: #444;
  font-size: 14px;
  max-width: 600px;

  a {
    text-decoration: underline;
    color: inherit;
  }
}

@mixin bg_options {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

// MENU

.menu {
  flex-wrap: wrap;
  max-width: 1920px;
  width: 100%;
  padding: 18px 100px 6px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  position: fixed;
  z-index: 11;
  background: #fff;
  border-bottom: 1px solid #e5e5e5;

  .burger {
    display: none;
  }

  .address {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 30px;

    &::before {
      content: "";
      width: 24px;
      height: 24px;
      display: block;
      margin-right: 16px;
      background-image: url('../images/icons/address.png');
      @include bg_options;
    }
  }

  .phones {
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    a {
      color: $text_black;
      font-size: calc(var(--index) * 0.9);
      line-height: 30px;
      font-weight: bold;
    }

    p {
      display: flex;
      align-items: center;
      font-size: calc(var(--index) * 0.6);
      line-height: 18px;
      margin-top: 6px;
      &:before{
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 10px;
        animation: header-phones alternate 2s infinite;
        box-shadow: 0 0 0 2px $yellow;
      }
    }
  }

  &_options {
    display: flex;

    div, a {
      color: $text_black;
      cursor: pointer;
      position: relative;
      margin-right: 18px;
      display: flex;
      flex-direction: column;
      align-items: center;

      &:last-child {
        margin-right: 0;
      }

      img {
        height: 20px;
        margin-bottom: 8px;
      }

      span {
        text-align: center;
        font-size: calc(var(--index) * 0.5);
        line-height: 120%;
      }

      .count_cart {
        position: absolute;
        top: -10px;
        right: -10px;
        padding: 10px;
        background-color: $yellow;
        color: $text_black;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 18px;
        font-size: 14px;
        height: 18px;
      }
    }

    &-active {
      border-bottom: 2px solid $yellow;

      img {
        box-shadow: 0px 1px 10px #FFF065;
      }
    }
  }
}

@keyframes header-phones {
  0%{
    box-shadow: 0 0 0 1px $yellow;
  }
  100% {
    box-shadow: 0 0 0 4.5px $yellow;
  }
}

.header {
  &_logo {
    margin-left: 2rem;

    a {
      color: $text_black;
    }
  }

  &_links {
    //width: 100%;
    display: inline-flex;
    justify-content: space-between;
    transition: all .5s ease-in-out;
    padding: 0 15px;

    a  {
      display: block;
      color: $text_black;
      font-size: calc(var(--index) * 0.6);
      margin-right: 32px;
      transition: all .3s ease-in-out;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        color: $yellow;
      }
    }
    & > ul > li {
      font-size: calc(var(--index) * 0.6);
    }

    input {
      width: 230px;
      border-radius: 8px;
      border: 1px solid $text_black;
      font-size: 14px;
      padding: 5px 10px 5px 15px;
    }

    p {
      display: none;
    }

    &.-active {
      left: 0;
    }

    &_tap_icon {
      position: relative;

      &:before {
        content: "";
        display: block;
        position: absolute;
        width: 36px;
        height: 36px;
        background-image: url("../images/icons/tapGold.png");
        @include bg_options;
        top: -50%;
        left: -37px;
        transform: rotateZ(-90deg);
      }
    }

    &_wrapper {
      &::-webkit-scrollbar {
        width: 2px;
        background-color: #fff;
        height: 4px;
      }

      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #b0b0b0;
      }

      &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
        border-radius: 10px;
        background-color: #fbfbff;
      }
    }

    // выпадающее подменю
    &_submenu {
      cursor: pointer;
      a {
        display: block;
      }
      li {
        position: relative;
        display: block;
        margin-bottom: .5rem;
        &:hover > ul {
          display: grid;
          left: 0;
        }
        li:hover > ul {
          left: 80%;
        }
      }
      ul {
        position: absolute;
        background-color: #f9f9f9;
        width: max-content;
        grid-template-columns: 1fr 1fr;
        gap: 0 20px;
        left: -2000px;
      }
    }
    &_first_submenu{
      top: 100%;
      padding: 1rem;
      box-shadow: 0 0 20px 1px rgb(0 0 0 / 10%);
      border-radius: 1rem;
      z-index: 1;
      grid-template-columns: 1fr 1fr;
      gap: 0 20px;
      max-height: 70vh;
    }
    &_second_submenu {
      z-index: 2;
      top: 0;
      //top: -10px;
      padding: .5rem 1rem;
      box-shadow: 0 0 20px 1px rgb(0 0 0 / 10%);
      border-radius: 1rem;
    }

  }

  &-down-info {
    display: flex;
    align-items: start;
    gap: 1.5rem;
    margin-top: calc(var(--index) * 3);
    margin-bottom: calc(var(--index) * 3);
    border: 1px solid #E4E4E4;
    border-radius: 30px;
    padding: calc(var(--index) * 1.2) calc(var(--index) * 2.7);
    &.none-border {
      border: none;
    }
    p {
      flex-grow: 1;
    }
  }
}
