.block_icons {
  &_reduce_margin {
    margin-top: -100px;
  }
  gap: 24px;
  &_elem {
    text-align: center;
    img {
      width: 250px;
    }
    p {
      margin-top: 18px;
      font-weight: bold;
    }
  }
}


// ФИЛьТР
.filter {
  &_nav {
    gap: 20px 30px;

    &_elem {
      position: relative;
      align-self: start;
      height: auto;
      border: 1px solid #d9d9d9;
      border-radius: 30px;
      padding: 19px 9px 19px 30px;

      a {
        font-size: 16px;
        color: #777;
      }

      .-list {
        position: absolute;
        z-index: 1;
        background: #fff;
        width: 100%;
        left: 0;
        padding: inherit;
        padding-top: 6px;
        border: inherit;
        border-bottom-right-radius: 30px;
        border-bottom-left-radius: 30px;
        border-top: 0;
        top: 67%;
        max-height: 150px;
        overflow-y: auto;
        display: flex;
        flex-direction: column;

        &::-webkit-scrollbar {
          width: 10px;
          background-color: #f4f4f4;
        }

        &::-webkit-scrollbar-thumb {
          border-radius: 10px;
          background-color: #b9b7b7;
        }

        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
          border-radius: 10px;
          background-color: #f9f9fd;
        }
      }
    }
  }

  &_content {
    margin-top: 50px;
    grid-gap: 30px;

    &_elem {
      position: relative;
      border: 2px solid #e4e4e4;
      border-radius: 20px;
      padding: 30px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: start;
      &_name {
        cursor: pointer;
        color: $text_black;
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        margin-bottom: 24px;
        display: inline-block;
        &.yellow_line_text {
          overflow: initial;
          &:before {
            transform: translateX(0);
            height: 4px;
            bottom: -3px;
            width: 102%;
            left: 0;
          }
          &::after {
            content: none;
          }
        }
      }

      .-img {
        overflow: hidden;
        border-radius: 15px;
        margin-bottom: 16px;
        width: 100%;
        img {
          border-radius: 15px;
          object-fit: cover;
          height: 270px;
          width: 100%;
          margin-left: 0;
        }
      }
      .-bullits {
        font-weight: 400;
        font-size: calc(var(--index) * .6);
        width: 80%;
      }

      .-desc {
        position: relative;
        height: 104px;
        overflow: hidden;
        font-size: 14px;
        line-height: 150%;

        &.-active {
          height: auto;

          .desc_btn {
            transform: rotateZ(180deg);
          }
        }

        a {
          color: $text_black;
          display: block;
        }
      }

      .-econom {
        display: flex;
        justify-content: center;
        margin-top: 12px;
      }

      .-cena {
        color: $text_black;
        cursor: pointer;
        margin-top: 20px;
        width: 100%;
        border-radius: 30px;
        background-color: $yellow;
        text-align: center;
        padding: 10px;
        font-weight: bold;
      }

      &_options {
        margin-top: 18px;
        display: flex;
        justify-content: center;
        padding: 0 40px;

        div {
          cursor: pointer;
          box-sizing: content-box;
          width: 25px;
          height: 25px;
          @include bg_options
        }

        .kalkulyator {
          background-color: $yellow;
          padding: 1rem 2rem;
          border-radius: 60px;
          position: relative;
          //overflow: hidden;
          &::after {
            top: -8px;
            content: '';
            display: block;
            -ms-transform: rotate(25deg);
            transform: rotate(25deg);
            position: absolute;
            width: 14px;
            height: 80px;
            z-index: 2;
            opacity: .55;
            background: #fff;
            -webkit-animation: 2.8s ease-out infinite btn-effect;
            animation: 2.8s ease-out infinite btn-effect;
          }

          &::before {
            content: "";
            display: block;
            width: 50px;
            height: 50px;
            background-image: url("../images/icons/tapBlack.png");
            background-size: contain;
            background-repeat: no-repeat;
            position: absolute;
            right: -20px;
            bottom: -20px;
            z-index: 3;
          }

          img {
            width: 18px;
          }
        }
      }
    }
  }

  &_btn {
    background-color: transparent;
    border: 2px solid $yellow;
  }
}

// ЧАСТО СПРАШИВАЮТ
.spros {
  position: relative;

  .carousel__track {
    .carousel__slide {
      width: var(--carousel-slide-width, 30%);
      margin-right: 15px;
      margin-left: 15px;
    }
  }
}

.carousel__button.is-next, .carousel__button.is-prev {
  background-color: #fff;
  border: 1px solid #e5e5e5;
}

