// ФОТО ОТЧЕТЫ
.photo_otchet {
  grid-gap: 60px 100px;
  align-items: start;

  &_content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    button {
      color: #000;
      text-align: center;
      border: none;
      background-color: transparent;
      cursor: pointer;

      &:before {
        bottom: -2px;
        height: 6px;
        width: 105%;
      }
    }
  }

  &_slider {
    width: 100%;
    margin-top: 20px;
    position: relative;

    .carousel__slide {
      padding: 0;
    }

    &:before, &:after {
      z-index: 3;
      content: "";
      display: block;
      width: 100px;
      height: 100%;
      position: absolute;
      bottom: 0;
    }

    &:before {
      background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
      border-radius: 10px 0px 0px 10px;
      left: 0;
    }

    &:after {
      right: 0;
      background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
      border-radius: 10px 0px 0px 10px;
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }

    .carousel__button.is-next,
    .carousel__button.is-prev {
      z-index: 5;
      color: #fff;

      svg {
        height: 40px;
        width: 49px;
      }
    }

    a {
      width: 100%;
      display: block;
      max-height: 411px;
      overflow: hidden;
      border-radius: 10px;

      img {
        width: 100%;
        max-height: 340px;
        object-fit: cover;
        border-radius: 10px;
      }
    }
  }

  &_info {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 40px;

    p {
      margin-left: 15px;
      font-size: 16px;
    }

    div {
