// ТАБЛИЦА С ЦЕНАМИ
.price_table {
  max-height: 500px;
  border-radius: 10px;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.15);
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 4px;
    background-color: #f4f4f4;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background-color: #b9b7b7;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: #f9f9fd;
  }

  &_title {
    margin-bottom: 10px;
    cursor: pointer;
    position: relative;
    background: #F5F5F5;
    border-radius: 10px;
    padding: 20px 0 20px 60px;
    text-align: center;
    vertical-align: center;
    font-size: 24px;
    line-height: 38px;
    color: #000;
    font-weight: bold;
    width: 100% !important;

    .h3 {
      text-align: left;
      text-transform: none;
    }

    a {
      color: #000;
    }

    .price_table_drop_btn {
      display: flex;
      align-items: center;
      position: absolute;
      right: 100px;
      top: 50%;
      transform: translateY(-50%);
      width: 25px;
      height: 25px;
      cursor: pointer;

      i {
        display: block;
        width: 25px;
        height: 11px;
        @include bg_options;
        background-image: url("../images/icons/drop_arrow.png");
        transition: all .3s;
      }

      &.-active {
        i {
          transform: rotateZ(180deg);
        }
      }
    }
  }

  table {
    border-radius: 10px;
    width: 100%;
    overflow: hidden;

    td {
      padding: 13px 20px 13px 60px;
      text-align: center;

      a {
        color: $text_black;
      }

      &:first-child {
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        text-align: left;
        width: 60%;
      }

      &:last-child {
        border-bottom-right-radius: 10px;
        border-top-right-radius: 10px;
        width: 40%;
      }
    }

    tr:nth-child(even) {
      background: #eee;
    }

    thead tr  {
      cursor: pointer;
      position: relative;
      background: linear-gradient(180deg,#ffe091 0,#fff0ca 100%) !important;
      td {
        text-align: center !important;
        background: none;
      }
      &.-active::after {
        transform: translateY(-50%) rotateX(180deg);
      }
      &:after {
        content: "";
        display: block;
        background-image: url("../images/icons/drop_arrow_black.png");
        width: 25px;
        height: 11px;
        top: 50%;
        transform: translateY(-50%);
        right: 48px;
        position: absolute;
        transition: all .3s ease-in-out;

      }
    }
  }
}

