// КАЛЬКУЛЯТОР
.calc {
  &_elem {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 40px;
    margin-bottom: 40px;
    align-items: center;

    &_info {
      .-name {
        font-size: 26px;
        line-height: 32px;
      }

      .-sub_name {
        font-size: 18px;
        line-height: 32px;
      }
    }

    &_range {
      position: relative;
      display: flex;
      flex-wrap: wrap;

      input[type=range] {
        padding-top: 40px;
        -webkit-appearance: none;
        margin: 10px 0;
        width: 100%;
      }

      input[type=range]:focus {
        outline: none;
      }

      // вся полоска
      input[type=range]::-webkit-slider-runnable-track {
        width: 100%;
        height: 9px;
        cursor: pointer;
        box-shadow: 0px 0px 0px #000;
        background: #ffc93e;
        border-radius: 10px;
        border: 0px solid #010101;
      }

      // кружочек
      input[type=range]::-webkit-slider-thumb {
        border: 2px solid #fff;
        box-sizing: border-box;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.12), inset 0px 2px 2px rgba(0, 0, 0, 0.25);
        height: 24px;
        width: 24px;
        border-radius: 25px;
        background: #ffc93e;
        cursor: pointer;
        -webkit-appearance: none;
        margin-top: -8px;
      }

      input[type=range]:focus::-webkit-slider-runnable-track {
        background: #ffc93e;
      }

      input[type=range]::-moz-range-track {
        width: 100%;
        height: 9px;
        cursor: pointer;
        animate: 0.2s;
        box-shadow: 0px 0px 0px #000;
        background: #ffc93e;
        border-radius: 10px;
        border: 0px solid #010101;
      }

      input[type=range]::-moz-range-thumb {
        box-shadow: 0px 0px 2px #292929;
        border: 1px solid #fff;
        height: 24px;
        width: 24px;
        border-radius: 25px;
        background: #ffc93e;
        cursor: pointer;
      }

      input[type=range]::-ms-track {
        width: 100%;
        height: 9px;
        cursor: pointer;
        animate: 0.2s;
        background: transparent;
        border-color: transparent;
        color: transparent;
      }

      input[type=range]::-ms-fill-lower {
        background: #ffc93e;
        border: 0px solid #010101;
        border-radius: 20px;
        box-shadow: 0px 0px 0px #000;
      }

      input[type=range]::-ms-fill-upper {
        background: #ffc93e;
        border: 0px solid #010101;
        border-radius: 20px;
        box-shadow: 0px 0px 0px #000;
      }

      input[type=range]::-ms-thumb {
        margin-top: 1px;
        box-shadow: 0px 0px 2px #292929;
        border: 1px solid #fff;
        height: 24px;
        width: 24px;
        border-radius: 25px;
        background: #ffc93e;
        cursor: pointer;
      }

      input[type=range]:focus::-ms-fill-lower {
        background: #ffc93e;
      }

      input[type=range]:focus::-ms-fill-upper {
        background: #ffc93e;
      }

      .-range {
        margin-top: 5px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        font-size: 16px;
      }

      .-range_value {
        position: absolute;
        padding: 4px 15px;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        top: 0;
        left: 0;
        transform: translateX(-20%);
      }
    }
  }

  &_itog {
    border-top: 3px solid #e5e5e5;
    margin: 80px auto;
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;

    p {
      font-size: 28px;
      text-align: center;
    }

    b {
      margin-top: 20px;
      font-size: 36px;
      text-align: center;
      display: block;
    }

    .-form {
      width: 100%;
      max-width: 1000px;
      margin: 40px auto 30px;
      display: flex;
      align-items: center;

      input {
        flex-grow: 1;
        margin-right: 30px;
      }
    }
  }
}

